import * as PIXI from 'pixi.js';
import mouse from './common/Mouse';

const _ = {
    app: null,

    currentColor: {
        r: 0,
        g: 0,
        b: 0,
        a: 255
    },
    background:[],

    width: 0,
    height: 0,
    scale : 0,

    debug : false,
    debugElem : document.getElementById('debug'),

    pixels :[],
    showGrid : false,
    refresh : true,

    doBoundUpdate : true,
    bounds : null,

    type : "WebGL",

    loader : PIXI.Loader.shared,
    Sprite : PIXI.Sprite,
    Graphics : PIXI.Graphics,

    updateBounds: () => {
        let canvasBounds = _.app.view.getBoundingClientRect();

        _.bounds = {
            x: canvasBounds.x,
            y: canvasBounds.y,
            left: canvasBounds.left,
            right: canvasBounds.right,
            top: canvasBounds.top,
            bottom: canvasBounds.bottom,
            width: canvasBounds.width,
            height: canvasBounds.height
        };

        _.doBoundUpdate = false;

        _.refresh = true;
    },

    /**
     * 
     * @param {PIXI.Application} app 
     * @returns {HTMLCanvasElement}
     */
    startup: () => {
        _.onReady(() => _.app.ticker.add(() => _.draw()));

        return _.app.view;
    },

    onReady: (setup) => {
        console.log('App ready. Loading.');

        if (!PIXI.utils.isWebGLSupported()) {
            _.type = "canvas";
        }

        PIXI.utils.sayHello(_.type);

        _.loader
            .load(setup);
    },

    updateDebug: () => {
        if (_.debugElem === null)
            _.debugElem = document.getElementById('debug');

        if (!_.debug) {
            _.debugElem.innerHTML = "";
            return;
        }

        let mousePos = mouse.getPosition();

        _.debugElem.innerHTML =
            `Mouse: ${JSON.stringify(mousePos)} --> ${Math.floor(mousePos.x / 16)}, ${Math.floor(mousePos.y / 16)}<br />` +
            `Color: ${JSON.stringify(_.currentColor)}<br />` +
            `0x${_.decToHex(_.currentColor.r)}${_.decToHex(_.currentColor.g)}${_.decToHex(_.currentColor.b)}`;
    },

    draw: () => {
        _.updateDebug();

        for (let x = 0; x < 16; x++) {
            for (let y = 0; y < 16; y++) {
                if (_.refresh)
                    _.setPixel(x, y, _.pixels[x][y].color);

                if (_.pixels[x][y].update) {
                    console.log(x, y, _.pixels[x][y].color);
                    _.app.stage.removeChild(_.pixels[x][y].graphic);

                    if (_.pixels[x][y].color.a < 255)
                        _.app.stage.addChild(_.background[y * 16 + x]);
                    else
                        _.app.stage.removeChild(_.background[y * 16 + x]);

                    if (_.pixels[x][y].color.a > 0)
                        _.app.stage.addChild(_.pixels[x][y].graphic);

                    _.pixels[x][y].update = false;
                }
            }
        }

        if (mouse.button.state) {
            let pos = mouse.getPosition();
            let x = Math.floor(pos.x / _.scale),
                y = Math.floor(pos.y / _.scale);

            if (x >= 0 && x < 16 && y >= 0 && y < 16) {
                if (mouse.button.id === 0)
                    _.setPixel(x, y, _.currentColor);
                else if (mouse.button.id === 2)
                    _.setPixel(x, y, { r: 0, g: 0, b: 0, a: 0 });

                _.getPixel(x, y).update = true;
            }

            if (!mouse.inBounds)
                mouse.button.state = false;
        }

        _.refresh = false;
    },

    decToHex: (c) => {
        var hex = Number(c).toString(16);
        return hex.length === 1 ? `0${hex}` : hex;
    },

    clear: () => {
        for (let x = 0; x < 16; x++) {
            for (let y = 0; y < 16; y++) {
                _.setPixel(x, y, null);
            }
        }
    },

    rgbToHex: (r, g, b) => {
        return Number(`0x${_.decToHex(r)}${_.decToHex(g)}${_.decToHex(b)}`);
    },

    getPixel: (x, y) => {
        return !_.pixels[x] ? null : !_.pixels[x][y] ? null : _.pixels[x][y];
    },

    setPixel: (x, y, color) => {
        if (!_.pixels[x][y]) {
            _.pixels[x][y] = {
                color,
                graphic: new _.Graphics()
            }
        }

        _.pixels[x][y].graphic = new _.Graphics();

        if (_.showGrid)
            _.pixels[x][y].graphic.lineStyle(1, 0x000000, 1);

        if (color === null)
            _.pixels[x][y].graphic.beginFill(0x000000);
        else
            _.pixels[x][y].graphic.beginFill(_.rgbToHex(color.r, color.g, color.b));

        _.pixels[x][y].graphic.drawRect(0, 0, _.scale, _.scale);

        _.pixels[x][y].graphic.endFill();

        _.pixels[x][y].graphic.alpha = color === null ? 0 : color.a / 255;

        _.pixels[x][y].graphic.x = x * _.scale;
        _.pixels[x][y].graphic.y = y * _.scale;

        _.pixels[x][y].update = true;

        _.pixels[x][y].color = color === null ? {r: 0, g: 0, b: 0, a: 0} : color;

        return _.pixels[x][y];
    },

    compose: () => {
        const canvas = document.createElement('canvas'), context = canvas.getContext('2d');
        canvas.width = 16;
        canvas.height = 16;

        let imgData = context.createImageData(16, 16);

        let color;
        for (let x = 0; x < 16; x++) {
            for (let y = 0; y < 16; y++) {
                let v = (y * 16 + x) * 4;
                color = _.getPixel(x, y).color;

                imgData.data[v] = color.r;
                imgData.data[v + 1] = color.g;
                imgData.data[v + 2] = color.b;
                imgData.data[v + 3] = color.a;
            }
        }

        context.putImageData(imgData, 0, 0);

        const url = canvas.toDataURL('image/png');
        let link = document.createElement('a');
        link.href = url;
        link.setAttribute('download', 'texture.png');
        link.click();
    },

    /**
     * 
     * @param {PIXI.Application} app 
     */
    create: (app) => {
        _.app = app;

        _.width = app.view.width;
        _.height = app.view.height;
        _.scale = _.width / 16;

        _.pixels = [];

        for (let x = 0; x < 16; x++) {
            _.pixels[x] = [];
            for (let y = 0; y < 16; y++) {
                _.background[y * 16 + x] = new _.Graphics();
                _.background[y * 16 + x].beginFill((x % 2 === 0 && y % 2 === 1) || (x % 2 === 1 && y % 2 === 0) ? 0xFFFFFF : 0xDDDDDD);
                _.background[y * 16 + x].drawRect(0, 0, _.scale, _.scale);
                _.background[y * 16 + x].endFill();

                _.background[y * 16 + x].x = x * _.scale;
                _.background[y * 16 + x].y = y * _.scale;

                _.setPixel(x, y, { r: 255 * x / 15, g: 0, b: 255 * y / 15, a: Math.floor((255 * x / 15 + 255 * y / 15) / 2) });
            }
        }

        return _;
    }
}

const editor = _;

export {
    editor
}